@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital@1&display=swap" rel="stylesheet');

*   {
    margin: 0;
    padding: 0;
}

body {
    background-color: #333;
    color: #fff;
    font-family:'Playfair Display';
    font-weight: 300;

}

.header {
    background-color: #222;
    padding: 25px 100px;
    font-weight: 600;
}

main{
    margin: 50px 100px;
    width: 50%;
    float: left;
}

aside {
    width: 20%;
    margin-top: 50px;
    margin-left: 5%;
    float:left;
}

main .user  {
    margin-bottom: 50px;
    background-color: #3e8fa0;
    padding: 20px;
}

main .delete-icon {
    float: right;
    color: #134b57;
    cursor: pointer;
    transform: scale(2);
}

main .edit-icon {
    float: right;
    margin-right: 25px;
    color: #165664;
    cursor: pointer;
    transform: scale(2);
}

main .delete-icon:hover,
main .edit-icon:hover {
    opacity: 0.5;
}

aside form input:none([type="checkbox"]),
aside form textarea,
aside form button {
    width: 90%;
    padding: 15px 5%;
    font-family:'Playfair Display';
    font-weight: 300;
    margin-bottom: 10px;
    border: 0;
    background: #222;
    color: #fff;
    outline: none;
}

aside form input[type="checkbox"]{
    margin: 10px;
}

aside form button{
    width: 100%;
    cursor: pointer;
    transition: transform 500ms ease;
}

aside form button:hover {
    transform: scale(1.1);
}

.header button {
    width: 5%;
    padding: 15px 5%;
    font-family:'Playfair Display';
    font-weight: 300;
    margin-bottom: 10px;
    border: 0;
    background: rgb(68, 68, 68);
    color: #fff;
    outline: none;
}

.header button{
    width: 10%;
    cursor: pointer;
    transition: transform 500ms ease;
    
}